<template>
  <v-app-bar :clipped-left="$vuetify.breakpoint.lgAndUp" app color="blue darken-3" dark>
    <v-app-bar-nav-icon @click="draw"></v-app-bar-nav-icon>
    <v-toolbar-title style="width: 300px" class="ml-0 pl-4">
      <span class="hidden-sm-and-down">{{appname}}</span>
    </v-toolbar-title>
    <!-- <v-text-field
      flat
      solo-inverted
      hide-details
      prepend-inner-icon="mdi-magnify"
      label="Search"
      class="hidden-sm-and-down"
    ></v-text-field>-->
    <v-spacer></v-spacer>
    <!-- <v-btn icon>
      <v-icon>mdi-apps</v-icon>
    </v-btn>-->
    <v-btn icon @click="logout">
      <v-icon>mdi-box</v-icon>
    </v-btn>
    <v-progress-circular v-if="isLoading" indeterminate color="amber"></v-progress-circular>
  </v-app-bar>
</template>

<script>
export default {
  name: "Appbar",
  computed: {
    counter() {
      return this.$store.getters.getCounter;
    },
    appname() {
      return this.$store.getters.appname;
    },
    isLoading() {
      return this.$store.getters.isLoading;
    },
  },
  mounted() {},
  methods: {
    draw() {
      this.$store.commit("sidebaract");
    },
    logout: function () {
      this.$store.dispatch("authLogout").then(() => {
        this.$router.push("/login");
      });
    },
  },
};
</script>

