import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import VueAxios from 'vue-axios'

Vue.use(Vuex)
Vue.use(VueAxios, axios)

export default new Vuex.Store({
  state: {
    count: 1,
    sideopen: true,
    snackopen: true,
    appname: "Application Name",
    listitem: [],
    isLoading: false,
    token: localStorage.getItem('user-token') || '',
    status: '',
    resulthttp : null
  },
  getters: {
    isAuth: state => !!state.token,
    authStatus: state => state.status,
    resulthttp: state => state.resulthttp,
    isLoading: state => {
      return state.isLoading
    },
    appname: state => {
      return state.appname
    },
    listitem: state => {
      return state.listitem
    },
    getCounter: state => {
      return state.count
    },
    sidebaropen: state => {
      return state.sideopen
    },
    snackopen: state => {
      return state.snackopen
    }
  },
  mutations: {
    isLoading: (state, val) => {
      state.isLoading = val
    },
    appname(state, nama) {
      state.appname = nama
    },
    addlist(state, item) {
      state.listitem.push(item)
    },
    increment(state) {
      state.count++
    },
    sidebaract(state) {
      state.sideopen = state.sideopen == false ? true : false
    },
    snackact(state, val) {
      state.snackopen = val
    },
    resulthttp: (state, val) => {
      state.resulthttp = val
    },
  },
  actions: {
    authLogout() {
      return new Promise((resolve) => {
        localStorage.removeItem('usertoken')
        localStorage.removeItem('username')
        resolve()
      })
    },
    getdata({commit}) {
      const baseURI = `http://localhost/epapp/crud/getall?limit=10`;
      axios.get(baseURI).then((result) => {
        commit('resulthttp', result)
      });
    },
  },
  modules: {}
})