<template>
  <v-container>
    <Drawer />
    <Appbar /> 
    <v-container>
      <v-row>
        <v-col sm="3" v-for="item in result" :key="item.id">
          <v-card max-width="200" router-link :to="'home/' + item.id">
            <v-img class="white--text align-end" v-bind:src="item.thumbnailUrl"></v-img>
            <v-card-subtitle class="pb-0">{{item.title}}</v-card-subtitle>
          </v-card>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-btn large color="primary" @click="loadmore()">Load More</v-btn>
      </v-row>
    </v-container>
    <Footer /> 
  </v-container>
</template>

<script>
import Appbar from "@/components/Appbar.vue";
import Drawer from "@/components/Drawer.vue";
import Footer from "@/components/Footer.vue";

export default {
  name: "Home",
  components: {
    Appbar,
    Drawer,
    Footer,
  },
  props: {
    source: String,
  },
  data: () => ({
    result: null,
    initlimit: 10,
    plus : 10,
  }),
  methods: {
    getdata() {
      this.isLoading(true);
      const baseURI = `https://jsonplaceholder.typicode.com/photos?_limit=${this.initlimit}`;
      this.$axios.get(baseURI).then((result) => {
        this.result = result.data;
      this.isLoading(false);
      });
    },
    loadmore() {
      this.initlimit = this.initlimit + this.plus;
      this.getdata();
    },
    isLoading(param) {
      this.$store.commit("isLoading", param);
    },
  },
  mounted() {
    this.getdata();
  },
};
</script>