import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import axios from 'axios';
import helpers from '../helper/mainhelper.js';

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login.vue')
  },
  {
    path: '/home/:id',
    name: 'HomeDetails',
    meta: {
      requiresAuth: true
    },
    component: () => import('../views/Homedet.vue')
  },
  {
    path: '/crud',
    name: 'Crud',
    component: () => import('../views/Crud.vue')
  },
  {
    path: '/Table',
    name: 'Table',
    meta: {
      requiresAuth: true
    },
    component: () => import('../views/Table.vue')
  },
  {
    path: '/listpage',
    name: 'List Item',
    meta: {
      requiresAuth: true
    },
    component: () => import('../views/Listpage.vue')
  },
  {
    path: '/about',
    name: 'About',
    meta: {
      requiresAuth: true
    },
    component: () => import('../views/About.vue')
  },
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  let baseurl = helpers.baseurl()
  if (to.path !== '/login' && !localStorage.usertoken) {
    next({ path: '/login' })
    return true
  }
  let url = `${baseurl}auth/authcheck`;
  axios.get(url, {params: {
    token : localStorage.usertoken,
    username : localStorage.username,
  }}).then((result) => {
    let res = result.data;
    if (to.path !== '/login' && res.stts == "stop") {
      next({ path: '/login' })
    }
    else {
      next()
    }
  });
})

export default router